
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'



import axios from 'axios'
export default {
components: {
XButton,
XDialog
},
data() {
return {
link: '/train-opinion',
trainName: '',
undertakeDepartment: '',
startTime: '',
showDialogStyle: false
};
},

mounted () {
const vm = this;
const query = this.$route.query;
vm.GLOBAL.query = query;
axios.get('/activity/findByToken?token=' + query.token)
.then(function (resp) {
if (resp.data.code != 0) {
vm.showDialogStyle = true;
return;
}

const data = resp.data.data;
data.startTime = data.startTime.replace(/\s.*$/, '');

vm.GLOBAL.data = data;
console.log(vm.GlOBAL);
Object.assign(vm, {
trainName: data.trainName,
undertakeDepartment: data.undertakeDepartment,
startTime: data.startTime
});
})
.catch(function (err) {
console.log(err);
})
}
}




